.action-button {
  border: 0;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-user-select: none; /* for button */
     -moz-user-select: none;
      -ms-user-select: none;
  display: block;
  width: 100%;
  /* height: 3rem; */
  /* font-size: 1rem; */
  /* background: #DDD; */
  background-color: #2a2a2a;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  padding: 10px 15px;
  font-size: 0.875rem;
}
.action-button:hover {
  background-color: #BE1E2D;
}
.action-button:focus {
  outline: none;
}

h3.mh-custom-posts-small-title {
  font-size: 1rem;
}
